import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { v4 as uuidv4 } from 'uuid';
import Loader from 'react-loader-spinner';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';
import Searchbar from './components/Searchbar';
import SearchForm from 'components/Searchbar/SearchForm';
import ImageGallery from 'components/ImageGallery';
import ImageGalleryItem from 'components/ImageGallery/ImageGalleryItem';
import Button from 'components/Button/Button';
import Modal from 'components/Modal';

class App extends Component {
  static defaultProps = {};

  static propTypes = {};

  state = {
    largeImageURL:
      'https://cdn.pixabay.com/photo/2020/05/20/07/10/architecture-5195171_1280.jpg',
    showModal: false,
  };

  onChange = () => {};

  onLoadMore = () => {
    this.toggleModal();
  };

  toggleModal = () => {
    this.setState(({ showModal }) => ({
      showModal: !showModal,
    }));
  };

  render() {
    const { showModal, largeImageURL } = this.state;
    return (
      <>
        <Searchbar onSubmit="handleSubmit">
          <SearchForm onChange="onChange" />
        </Searchbar>
        <ImageGallery>
          <ImageGalleryItem />
        </ImageGallery>
        <Button onClick={this.onLoadMore}>
          <Loader
            type="ThreeDots"
            color="#fff"
            height={80}
            width={80}
            timeout={20000} //3 secs
          />
        </Button>
        {showModal && (
          <Modal>
            <img src={largeImageURL} alt="Lighthouse" />
          </Modal>
        )}
      </>
    );
  }
}

export default App;
